import design from './design.svg';

import infographic from './infographic.svg';
import motion from './motion.svg';
import presentation from './presentation.svg';
import interaction from './interaction.svg';
import expo from './expo.svg';

export default {
  icon: design,
  title: '設計',
  text: '訂定了好的策略後，還需要運用好的設計來呈現資訊！',
  content: [
    {
      icon: infographic,
      label: '資訊圖',
      text: '把資訊圖像化呈現，讓圖像成為幫忙傳遞資訊的重要工具。',
    },
    {
      icon: motion,
      label: '說明影片',
      text: '讓你的資訊動起來！多了聽覺的輔助，讓資訊吸收更快更有趣。',
    },
    {
      icon: presentation,
      label: '商業簡報',
      text: '使簡報更有說服力，讓你在台上更有自信！',
    },
    {
      icon: interaction,
      label: '互動資訊圖',
      text: '使用者能主動探索並篩選資訊，適用於資訊較為龐雜的狀況。',
    },
    {
      icon: expo,
      label: '展場空間設計',
      text: '專門為資訊設計出最適合的傳遞空間，讓有興趣的人能快速理解。',
    },
  ]
};



