import React from 'react';

import PageTitle from '../../components/PageTitle';
import Box from '../../components/Box';
import Text from '../../components/Text';
import Flex from '../../components/Flex';
import Border from '../../components/Border';
import BackgroundImage from '../../components/BackgroundImage';

import data from './data';
import re from './re.svg';

const renderContent = ({ icon, label, text }, index) => (
  <Flex key={index} my="2em" px="1em" alignItems="center">
    <Box w={[1 / 4, null, '6em']} mr="2em">
      <BackgroundImage src={icon} />
    </Box>
    <Box flex={1}>
      <Text f="1.25em">{label}</Text>
      <Text>{text}</Text>
    </Box>
  </Flex>
);

const renderSection = ({ icon, title, text, content }, index) => (
  <Flex
    is={Border}
    borderBottom="2px solid"
    borderColor="lightGray"
    py="2em"
    flexDirection={['column', null, 'row']}
    key={index}
  >
    <Box align="center" w={[1, null, '20em']} px="2em">
      <Box mx="4em">
        <BackgroundImage src={icon} />
      </Box>
      <Text f="1.5em" color="blue" my="1rem" fontWeight="bold">{title}</Text>
      <Text f="1.25em">{text}</Text>
    </Box>
    <Box flex={1} mt={['2em', null, 0]}>
      {content.map(renderContent)}
    </Box>
  </Flex>
);

const About = (props) => {
  return (
    <Box py="4em" {...props}>
      <PageTitle
        icon={re}
        sub="我們提供從策略到設計、推廣的完整服務。"
      >
        我們的服務
      </PageTitle>
      {data.map(renderSection)}
    </Box>
  );
};

export default About;



